import React, { ReactNode, useState, useEffect } from 'react';
// import { Link } from 'react-router-dom';
import './SideBar.scss';

interface Props {
	inverseColor?: boolean;
	pageProgess: any;
	updatePageProgess: (status: string) => void;
	scrollToSec: (key: string) => void;
}

export const SideBar = ({
	pageProgess, updatePageProgess, inverseColor, scrollToSec,
}: Props) => {
	const [activeSection, setActiveSection] = useState<string>('');
	const [menuOpen, setMenuOpen] = useState<boolean>(false);

	useEffect(() => {
		// Determine active section
		if (pageProgess.artisan) {
			setActiveSection('artisan');
		} else if (pageProgess.gallery) {
			setActiveSection('gallery');
		} else if (pageProgess.tour) {
			setActiveSection('tour');
		} else if (pageProgess.exhibition) {
			setActiveSection('exhibition');
		} else {
			setActiveSection('journey');
		}
	}, [activeSection, pageProgess]);

	const onClickNav = (key: string) => {
		scrollToSec(key);
		setMenuOpen(false);
	};

	return (
		<div className={`sidebar ${inverseColor ? 'sidebar--inverse-color' : ''} ${menuOpen ? 'sidebar--menu-open' : ''}`}>
			<div className="sidebar__overlay" />
			<div className="sidebar__inner">
				<div className="sidebar__top">
					<button
						type="button"
						onClick={() => { setMenuOpen(!menuOpen); }}
						className="sidebar__menu-btn"
					>
						<span className="sidebar__menu-icon">
							<span className="sidebar__menu-lines" />
						</span>
						<span className="sidebar__menu-text">
							{menuOpen ? 'Open menu' : 'Close menu'}
						</span>
					</button>
					<div className="sidebar__title">
						<div className="sidebar__title-fragment">NWD</div>
						<div className="sidebar__title-fragment">50TH ANNIVERSARY</div>
					</div>
				</div>
				<nav className="sidebar__navs">
					<button
						type="button"
						onClick={() => { onClickNav('journey'); }}
						className={`sidebar__nav ${activeSection === 'journey' ? 'is-active' : ''}`}
					>
						<span
							className="sidebar__nav-label"
						>
					50 Years of Journey
						</span>
					</button>
					<button
						type="button"
						onClick={() => { onClickNav('exhibition'); }}
						className={`sidebar__nav ${activeSection === 'exhibition' ? 'is-active' : ''}`}
					>
						<span
							className="sidebar__nav-label"
						>
					THE EXHIBITION
						</span>
					</button>
					<button
						type="button"
						onClick={() => { onClickNav('tour'); }}
						className={`sidebar__nav ${activeSection === 'tour' ? 'is-active' : ''}`}
					>
						<span
							className="sidebar__nav-label"
						>
VIRTUAL TOUR
						</span>
					</button>
					<button
						type="button"
						onClick={() => { onClickNav('gallery'); }}
						className={`sidebar__nav ${activeSection === 'gallery' ? 'is-active' : ''}`}
					>
						<span
							className="sidebar__nav-label"
						>
					GALLERY
						</span>
					</button>
					<button
						type="button"
						onClick={() => { onClickNav('artisan'); }}
						className={`sidebar__nav ${activeSection === 'artisan' ? 'is-active' : ''}`}
					>
						<span
							className="sidebar__nav-label"
						>
					BECOME AN ARTISAN
						</span>
					</button>
				</nav>
				<nav className="sidebar__bottom">
					<button
						type="button"
						className="sidebar__join"
					>
						<span
							className="sidebar__join-label"
						>
					BECOME AN ARTISAN
						</span>
					</button>
					<button
						type="button"
						className="sidebar__soc sidebar__soc--fb"
					>
						<span
							className="sidebar__soc-label"
						>
					Facebook
						</span>
					</button>
					<button
						type="button"
						className="sidebar__soc sidebar__soc--ig"
					>
						<span
							className="sidebar__soc-label"
						>
					Instagram
						</span>
					</button>
				</nav>
			</div>
		</div>
	);
};


export default SideBar;

