import React, { useEffect, useState } from 'react';
import LoadingBar from 'react-top-loading-bar';
import { disableBodyScroll, enableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock';
import { CubeNumber } from '../../components';
import './Splash.scss';

interface Props {
	pageProgess: any;
	updatePageProgess: (status: string) => void;
}

const Splash = ({ pageProgess, updatePageProgess }: Props) => {
	const [loadingProgress, setLoadingProgess] = useState(0);
	const [animationStep, setAnimationStep] = useState<number>(0);
	const [splashCount, setSplashCount] = useState<string>('38');
	const dummyDigits = [0, 3, 5, 8];
	const getDummyNumber = () => dummyDigits[Math.floor(Math.random() * dummyDigits.length)];

	// Config animation
	const aniTimeSplashStep1 = 2; // Logo enlarge
	const aniTimeSplashStep2 = 0.6; // Logo Still
	const aniTimeSplashStep3 = 0.8; // page ready, logo move top right
	document.documentElement.style.setProperty('--ani-time-splash-step-1', `${aniTimeSplashStep1}s`);
	document.documentElement.style.setProperty('--ani-time-splash-step-2', `${aniTimeSplashStep2}s`);
	document.documentElement.style.setProperty('--ani-time-splash-step-3', `${aniTimeSplashStep3}s`);

	useEffect(() => {
		// Animation Steps Control
		if (animationStep === 0) {
			disableBodyScroll(document.body);
			window.scrollTo({
				top: 1000,
				behavior: 'auto',
			});
			setAnimationStep(1);
		}
		if (animationStep === 1) {
			setLoadingProgess(100);
			setTimeout(() => {
				setAnimationStep(2);
				enableBodyScroll(document.body);
				window.scrollTo({
					top: 30,
					behavior: 'auto',
				});
			}, aniTimeSplashStep1 * 1000);
		}
		if (animationStep === 2) {
			setTimeout(() => {
				setAnimationStep(3);
				window.scrollTo({
					top: 1,
					behavior: 'auto',
				});
			}, aniTimeSplashStep2 * 1000);
		}
		if (animationStep === 3) {
			if (!pageProgess.pageReady) {
				updatePageProgess({
					...pageProgess,
					pageReady: 'ready',
					splash: 'finish',
				});
			}
		}
	}, [animationStep, pageProgess, updatePageProgess]);

	useEffect(() => {
		const countChangeInterval = setInterval(() => {
			const dummyCount = (animationStep === 1) ? `${getDummyNumber()}${getDummyNumber()}` : '50';
			setSplashCount(dummyCount);
		}, 150);
		return () => {
			clearInterval(countChangeInterval);
		};
	}, [animationStep, getDummyNumber]);

	return (
		<>
			<LoadingBar
				height={8}
				color="#D8182A"
				shadow={false}
				progress={loadingProgress}
				waitingTime={(aniTimeSplashStep1 + aniTimeSplashStep2 + aniTimeSplashStep3 / 2) * 1000}
				loaderSpeed={(aniTimeSplashStep1 + aniTimeSplashStep2) * 1000}
			// onLoaderFinished={() => setProgress(0)}
			/>
			<div
				className={`splash splash--step-${animationStep}`}
			>
				<div
					className="splash__overlay"
					style={{
						animationDuration: `${(aniTimeSplashStep1 + aniTimeSplashStep2)}s`,
					}}
				/>
				<div className="splash__inner">
					<div className="splash__logo">
						<CubeNumber number={splashCount} />
						<div className="splash__static-logo" />
					</div>
				</div>
			</div>
		</>
	);
};

export default Splash;
