import React, { ReactNode } from 'react';
import { useScrollPercentage } from 'react-scroll-percentage';
import './StairText.scss';

interface Props {
	text: [string, string];
	// children?: ReactNode;
	// initialPoint?: number;
	// endPoint?: number;
}

export const StairText = (
	{ text }: Props,
) => {
	// Scroll Monitor
	const [ref, percentage] = useScrollPercentage({
		/* Optional options */
		threshold: 0,
	});
	const processPercentage = parseFloat(percentage.toPrecision(2));

	return (
		<div ref={ref} className={`stair-text ${processPercentage > 0.1 ? 'is-active' : ''}`}>
			<div
				className="stair-text__content"
			>
				{/* <span className="stair-text__positioner"> */}
				<span className="stair-text__line">
					<span className="stair-text__txt">&nbsp;</span>
					<span className="stair-text__txt">{text[0]}</span>
				</span>
				<span className="stair-text__line">
					<span className="stair-text__txt color-main">{text[0]}</span>
					<span className="stair-text__txt color-main">{text[1]}</span>
				</span>
				<span className="stair-text__line">
					<span className="stair-text__txt">{text[1]}</span>
					<span className="stair-text__txt">&nbsp;</span>
				</span>
				{/* </span> */}
			</div>
		</div>
	);
};

export default StairText;
